.c-hero-rewards {
  background-color: $s-color--background-subdued;
  padding-bottom: 1.25rem;
  overflow: hidden;

  &__outer {
    background-color: $s-color--background-impact;
  }

  &__inner {
    background-color: $s-color--background-impact;
    position: relative;
    padding: 9.375rem $s-gutter--edge-mobile 10rem;
    z-index: $s-z--text-overlay;
  }

  &__illustration-1 {
    position: absolute;
    width: 6.875rem;
    top: 1.125rem;
    left: -3.25rem;
  }

  &__illustration-2 {
    position: absolute;
    width: 13.3125rem;
    top: 8.875rem;
    right: -9.8125rem;
  }

  &__illustration-3 {
    display: none;
  }

  &__pretitle {
    text-align: center;
    color: $s-color--font-invert;
  }

  &__greeting {
    text-align: center;
    color: $s-color--font-invert;
    margin-bottom: 1.6875rem;
  }

  &__subtitle {
    text-align: center;
    color: $s-color--font-invert;
    transition: all 0.3s;

    &.is-hidden {
      opacity: 0;
    }
  }

  &__rewards-meter {
    position: absolute;
    aspect-ratio: 1;
    width: 64%;
    max-width: 20.5rem;
    margin: 0 auto;
    transform: translate(-50%, 50%);
    left: 50%;
    bottom: 0;
  }

  &__outer-circle {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    @include t-absolute-center-x-y();
    background: conic-gradient(#bcb0ac 100%, $s-color--border-progress-fill 0);
    z-index: $s-z--reset;
  }

  &__inner-circle {
    display: block;
    width: 95%;
    height: 95%;
    border-radius: 50%;
    @include t-absolute-center-x-y();
    background-color: $s-color--background-subdued;
    z-index: $s-z--text-overlay;
  }

  &__meter-text {
    @include t-absolute-center-x-y();
    z-index: $s-z--illustration-overlay;
    width: 80%;
    text-align: center;
  }

  &__next-reward {
    margin-bottom: 0;
    transition: all 0.3s;
    transform: scale(1);

    &.is-hidden {
      opacity: 0;
      transform: scale(0.9);
    }

    &.is-unembiggened {
      font-size: 1.5rem;
      width: 90%;
      margin: 0 auto;
    }
  }

  &__image-wrapper {

    &--1,
    &--2,
    &--3 {
      display: none;
    }
  }

  &__reward-text {
    transition: all 0.3s;
    margin: 0;

    &.is-hidden {
      opacity: 0;
    }
  }

  &__content {
    font-size: 1.125rem;
    padding: 0 $s-gutter--edge-mobile;
    text-align: center;
    max-width: 35rem;
    margin: 10.5rem auto 0;
  }

  .o-wave__wave {
    transition: transform 5s ease-in-out;
  }

  @include t-media-query($s-medium-up) {
    &__image-wrapper {
      &--1 {
        display: block;
        width: 19rem;
        position: absolute;
        top: 10.875rem;
        left: 3.2%;
      }

      &--2 {
        display: block;
        width: 19.5625rem;
        position: absolute;
        bottom: -11.875rem;
        right: -3.375rem;

        .c-hero-rewards__image-container {
          padding-bottom: 138.6%;
        }
      }

      &--3 {
        display: block;
        width: 11rem;
        position: absolute;
        bottom: -2.875rem;
        left: -1.5625rem;
        z-index: $s-z--illustration-overlay;
      }
    }

    &__greeting {
      margin: 1rem 0 1.375rem;
    }

    &__meter-text{
      top: 51%;
    }

    &__next-reward {
      margin-bottom: 0.5rem;
      &.is-unembiggened {
        font-size: 2.5rem;
        margin-bottom: 0.4375rem;
      }
    }
  }

  @include t-media-query($s-large-up) {

    &__image-wrapper {
      &--1 {
        width: 22rem;
      }

      &--2 {
        width: 22.5625rem;
      }

      &--3 {
        bottom: -5.875rem;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding-bottom: 5rem;

    &__inner {
      padding: 16.4375rem 0 14.375rem;
      max-width: 102.5rem;
      margin: 0 auto;
    }

    &__image-wrapper {
      &--1 {
        width: 25rem;
      }

      &--2 {
        width: 25.5625rem;
      }

      &--3 {
        width: 14rem;
        left: -3.5625rem;
        bottom: -2.875rem;
      }
    }

    &__illustration-1 {
      position: absolute;
      width: 7.875rem;
      top: initial;
      bottom: 0.125rem;
      left: 8.75rem;
      transform: rotate(298deg);
      z-index: $s-z--text-overlay;
    }

    &__illustration-2 {
      display: none;
    }

    &__illustration-3 {
      display: block;
      position: absolute;
      z-index: $s-z--reset;
      width: 32.5rem;
      right: -11.125rem;
      top: 4.4375rem;
    }

    &__next-reward {
      font-size: 3rem;
    }

    &__content {
      margin-top: 8rem;
    }
  }
}